@import url("https://fonts.googleapis.com/css2?family=Concert+One&display=swap");
body {
  font-family: "Concert One", cursive !important;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  padding: 1rem;
  background-color: #232946 !important;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.content {
  padding: 2rem;
}

.tag {
  float: right;
}

h1 {
  color: hotpink;
  padding: 2rem;
}

h3 {
  color: #8bd3dd;
  padding: 1rem;
}

footer {
  color: #b8c1ec;
  padding: 3rem;
}

footer a {
  text-decoration: none;
  color: hotpink;
}

footer a:hover {
  text-decoration: none;
  color: teal;
}
